<template>
	<div class="wrapper">
	<dl  class="info-item info-small no-border" >
		<dt >
			<img  :src="aladdin.cover" alt="">
		</dt>
		<dd  class="info-title">{{this.$route.params.novalName}}</dd>
		<dd class="info-author"><i class="icon iconfont icon-31wode"></i>{{aladdin.author}}</dd>
		<dd class="info-category">{{aladdin.category}}</dd>
		<dd class="info-tags">{{aladdin.tags}}</dd>
		 <dd  class="info-note">
			{{aladdin.desc}}
		</dd> 
	</dl>
	<div class="similar">
		<h2>同类书籍</h2>
		<div class="nav">
      		<div class="nav-bg"></div>
	     		<div class="nav-list">
					<ul class="clearfix">
						<li v-for="(item,index) in lists" >
								<div class="pic"><img :src="item.cover" alt=""></div>
								<div class="desc">{{item.title}}</div>
								<div class="author">{{item.author}}</div>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</div>
</template>
<script>
	export default {
		data(){
			return{
				aladdin:{},
				lists:[],
			}
		},
		methods:{

		},
		created(){
			this.$axios.get("https://www.apiopen.top/novelInfoApi?name="+ this.$route.params.novalName +"")
			.then(res=>{
				this.aladdin = res.data.data.aladdin;	
				this.lists = res.data.data.data;
			})
			.catch(err=>{
				console.log(err);
			})
		},

	}
</script>
<style scoped>
img{
  width: 100%;
}
.wrapper{
	padding: 0 12px 0;
}
	  .info-list {
    padding: 0 12px;
  }

  .info-small {
    position: relative;
    height: 180px;
    padding: 12.5px 0 12.5px 125px;
  }

  .info-small dt {
    position: absolute;
    top: 12.5px;
    left: 0;
    width: 115px;
    height: 150px;
    overflow: hidden;
  }

  .info-item dt img {
    width: 100%;
    height: 100%;
  }

  .info-title {
    font-size: 15px;
    line-height: 24px;
    color: #000;
  }

  .info-small .info-title {
    height: 30px;
    line-height: 30px;
    font-size: 18px;
    font-weight: 700;
    overflow: hidden;
  }

   .info-small .info-author{ 
    font-size: 12px;
    color: #ccc;
    height: 20px;
    overflow: hidden;
	line-height: 20px;
    }
    .info-small .info-author i{
		margin-right: 3px; 
    }
   .info-small .info-category{ 
    font-size: 12px;
    color: #ccc;
    height: 20px;
    overflow: hidden;
    margin: 1px 0;
    }
      .info-small  .info-tags{
      	   font-size: 12px;
    color: #ccc;
    height: 20px;
    overflow: hidden;
    margin: 1px 0;
      }
  .info-note {
    margin-top: 5px;
    height: 20px;
    line-height: 20px;
    font-size: 10px;
    color: #999;
    overflow: hidden;
  }

  .left {
    float: left;
  }

  .right {
    float: right;
  }

  .cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  a {
    color: #333;
    text-decoration: none;
  }

  .info-three {
    padding: 8px 0;
  }

  .info-item {
    position: relative;
    border-bottom: 1px solid #ddd;
  }

  .info-pic-list {
    margin-top: 5px;
    overflow: hidden;
  }

  .info-pic-list img {
    float: left;
    width: 32%;
  }

  .info-pic-list img + img {
    margin-left: 2%;
  }

  .info-note {
    margin-top: 5px;
    height: 55px;
    line-height: 19px;
    font-size: 10px;
    color: #999;
    overflow: hidden;
  }

.similar h2{ margin:  5px 0; }
  .nav {
    position: relative;
    width: 100%;
    height: 205px;
    overflow: hidden;
  }

  .nav-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }

  .nav-list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height:220px;
    overflow-x: scroll;
    overflow-y: hidden; 
  }

  .nav ul {
    position: relative;
    white-space: nowrap;
    height: 205px;
    z-index: 0;

  }

  .nav ul li {
    display: inline-block;
    height: 205px;
 width: 115px;
    text-align: center;
    margin: 0 5px;vertical-align:top;
  }
    .nav ul li .pic{
    display: inline-block;
    height: 150px;
    width: 115px;
    text-align: center;
  }
  .nav ul li .pic img { width: 100%; height: 100%; }
.nav ul li .desc{ width: 100%; white-space:normal; font-size: 12px; color: #ccc; height: 35px; line-height: 17px;}
.nav ul li .author { width: 100%;  white-space:normal; color: #000; font-size:  14px;}

  .nav ul li.act a {
    color: #733821;
  }

  .nav ul li a {
    display: inline-block;
    position: relative;
    font-size: 15px;
    color: #000;
  }
</style>



